import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex)

const initialState = {
    user: {},
    isLoggedIn: false,
    currentViewSt: 'Learn',
}

export default new Vuex.Store({
  state: initialState,
  mutations: {
    userMu(state, user) {
      state.user = user;
    },
    loginMu(state, isLoggedIn) {
      state.isLoggedIn = isLoggedIn;
    },
    currentViewMu(state, view) {
      state.currentViewSt = view;
    }
  },
  getters: {
    user(state) {
      return state.user;
    },
    currentView(state) {
      return state.currentViewSt
    },
    isLoggedIn(state) {
      return state.isLoggedIn;
    }
  },
  plugins: [createPersistedState({storage: window.sessionStorage, key: 'FlashCardApp'})]
})
