<script>
import { mapGetters } from 'vuex'
import Muuri from 'muuri'
import firebase from "firebase/app"


export default {
  components: {
  },
  name: 'Stock',
  data: function() {
    return {
      database: firebase.database(),
      cardPath: this.$route.params.cardsName.slice(1),
      get: {},
      cardName: '',
      grid: {},
      gridIf: true,
      filTxtDa: '',
      fusenDa: '',
      delShow: false,
    }
  },
  computed: {
    ...mapGetters([
        'user',
        'isLoggedIn'
    ]),
    keys: function(){
      return Object.keys(this.get);
    }
  },
  methods: {
    filTxtMe: function(){
      let that = this;
      this.grid.filter(function(x){
        return x.getElement().childNodes[0].innerText.indexOf(that.filTxtDa) >= 0;
      });
    },
    fusenFilMe: function(){
      let that = this;
      if(this.fusenDa != '') {
        this.grid.filter(function(x) {
        return x.getElement().childNodes[0].childNodes[0].classList[1] == that.fusenDa;
        })
      }else {
        this.grid.filter(function() {
          return true;
        })
      }
    },
    // deleteMe: function(elm_k) {

    //   if(this.keys.length > 0) {

        // let that = this;

        // this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' +this.keys[elm_k]).remove();
        // this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath).get().then(function(snap) {
        //   that.get = snap.toJSON().data;
        // });
        // this.$ons.notification.toast('カードを削除しました', { timeout: 1000, animation: 'fall' });
        // }
      


      // let that = this;
      // this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' + this.keys[elm_k]).remove();
      // let elms = document.getElementsByClassName('muuriItem');
      // for(let i = 0; i < elms.length; i++) {
      //   elms[i].id = i;
      // }
      // this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/').get()
      // .then(function(snap){
      //   let newSt = {};
      //   let copySt = snap.toJSON();
      //   let copyKeys = Object.keys(copySt);
      //   for(let i = 0; i < copyKeys.length; i++) {
      //     newSt[i] = copySt[copyKeys[i]];
      //   }
      //   that.database.ref('user/' + that.user.uid + '/cards/' + that.cardPath + '/data/').set(
      //     newSt
      //   );
      // })
    // },
    shuffleMe: function() {
      let that = this;
      this.database.ref('user/' + that.user.uid + '/cards/' + that.cardPath + '/data').get()
      .then(function(snap){
        let copySt = snap.toJSON();
        let copyKeys = Object.keys(copySt);
        let assigned = [];
        for(let nIdx = 0; nIdx < copyKeys.length; nIdx++) {
          let randNum = Math.floor(Math.random() * copyKeys.length);
            while(assigned.includes(randNum)) {
                randNum = Math.floor(Math.random() * copyKeys.length);
            }
          assigned.push(randNum);
          that.database.ref('user/' + that.user.uid + '/cards/' + that.cardPath + '/data/' + nIdx).set(
            copySt[randNum]
          );
          document.getElementsByClassName('muuriItem')[randNum].id = nIdx;
        }
      }).then(function() {
          that.grid.refreshSortData();
          that.grid.sort('sortNum');
          that.grid.synchronize();
      })
    }
  },
  filters: {
    cardItemFil: function(x) {
      let str = x;
      if(str.length > 10 && str != '') {
        str = str.slice(0, 10) + '…'
      }
      return str
    }
  },
  created: function() {
    let that = this;
    this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath).get().then(function(snap) {
      that.get = snap.toJSON().data;
      that.cardName = snap.toJSON().name;
    }).then(function() {
      
      that.grid = new Muuri('.grid', {dragEnabled: true, sortData: {
        sortNum: function(item, element) {
          return parseInt(element.childNodes[0].getAttribute('id'));
        }
      }
      }
      );

      //ドラッグ時の処理
      that.grid.on('dragEnd', function () {
        if(that.delShow) {
          return
        }
        that.grid.synchronize();
        let elms = document.getElementsByClassName('muuriItem');
        that.database.ref('user/' + that.user.uid + '/cards/' + that.cardPath + '/data').get()
        .then(function(snap) {
          let copySt = snap.toJSON();
          let copyKeys = Object.keys(copySt);
          let data = {};
          for(let i = 0; i < elms.length; i++) {
            data[copyKeys[i]] = copySt[elms[i].id];
          }
          that.database.ref('user/' + that.user.uid + '/cards/' + that.cardPath ).update({
            data
          }
          )
          for(let i = 0; i < elms.length; i++) {
            elms[i].id = i;
          }
        })
      });
    });
  },
}
</script>