<template id="home">
        <div class="topBg">
          <h1 class="topTitle">Flash Card App</h1>
          <div class="formWrapper">
            <div v-on:click.prevent>
                <v-ons-input class="inputs" type="email" placeholder="メールアドレス" v-model="mailaddress" v-on:keyup.enter="login"></v-ons-input>
                <v-ons-input class="inputs" type="password" placeholder="パスワード" v-model="password" v-on:keyup.enter="login"></v-ons-input>
              <div class="optFlex">
                <v-ons-button modifier="quiet" class="signupCl" v-on:click="signupShowMe"><ons-icon class="signupIcon" icon="fa-user-plus" size="1rem"/>新規登録</v-ons-button>
                <v-ons-button class="regBtn" v-on:click="login">ログイン</v-ons-button>
              </div>
            </div>

            <v-ons-dialog cancelable :visible.sync="signupShow" class="signupDialog">
              <p class="dialog-title">新規登録</p>
              <v-ons-input class="inputs" type="email" placeholder="メールアドレス" v-model="mailaddressNew" v-on:keyup.enter="signup"></v-ons-input>
              <v-ons-input class="inputs" type="password" placeholder="パスワード(英数字6文字以上)" v-model="passwordNew" v-on:keyup.enter="signup"></v-ons-input>
              <div class="dialogFlex">
                <v-ons-button modifier="quiet" class="signupBtn" v-on:click="signup">OK</v-ons-button>
                <v-ons-button modifier="quiet" class="signupBtn" v-on:click="signupShow = false">CANCEL</v-ons-button>
              </div>
            </v-ons-dialog>
            

          </div>
        </div>
      <!-- </router-link> -->
</template>


