<script>
import firebase from 'firebase'
import "firebase/app"
import  "firebase/auth"
import store from '../store/index.js'
import { mapGetters } from 'vuex'

export default {
  name: 'Home',
  template: '#home',
  data: function() {
    return {
      database: firebase.database(),
      userNameNew: '',
      mailaddress: '',
      mailaddressNew: '',
      password: '',
      passwordNew: '',
      signupShow: false,
    }
  },
  computed: {
    ...mapGetters([
        'currentView',
        'user'
    ])
  },
  methods: {

    login: function() {
      let that = this;
      firebase.auth().signInWithEmailAndPassword(this.mailaddress, this.password)
      .then( function() {
        store.commit('userMu', firebase.auth().currentUser);
        firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION);
        that.$ons.notification.toast('ログインしました', { timeout: 1500, animation: 'fall'})
        that.$router.push('Menu')
      })
      .catch(error => {
        if(error.message != 'Success!'){
          alert(error.message)
        }
      });
      //リファクタ跡。セッションがうまくいかなかった場合、バックアップをさかのぼること
    },
    signupShowMe: function() {
      this.signupShow = true;
    },
    signup: function () {{
      let that = this;
      firebase.auth().createUserWithEmailAndPassword(this.mailaddressNew, this.passwordNew)
        .then( function(){
          store.commit('userMu', firebase.auth().currentUser);
          let setObj = {
            userName: that.userNameNew
          }
          that.database.ref('user/' + that.user.uid).update(
            setObj
          )
          that.$ons.notification.toast('アカウントを作成しました', { timeout: 1500, animation: 'fall'})
        })
        .catch(error => {
          alert(error.message)
        }).then(function(){
      firebase.auth().signInWithEmailAndPassword(that.mailaddressNew, that.passwordNew)
      .then( function() {
        store.commit('userMu', firebase.auth().currentUser);
        firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION);
        that.$router.push('Menu')
      })
      .catch(error => {
        if(error.message != 'Success!'){
          alert(error.message)
        }
      });})
      }
    },
  },
}
</script>