<template>
    <div class="Learn">
        
        <div class="whole" v-on:click.prevent="toolLeaveMe">
            <div class="wrapper">
                <div class="cards" v-if="(cardIf && keys.length > 0)" v-bind:class="{editOn: editDa}">
                    <transition :name="flipName" mode="out-in">
                        <div class="omote card" v-bind:class="{editCard: editDa}" v-on:click.prevent.stop="sideFlip" v-if="sides" v-bind:key="sides">
                            <div class="fusenCl" v-bind:class="{greenCl : fusenCo == 'green', yellowCl : fusenCo == 'yellow', redCl : fusenCo == 'red'}"></div>
                            <img class="cardImg" v-show="image" v-bind:src="image" alt="画像を読み込めませんでした。">
                            <div class="contentSt conOmote" v-bind:class="{editSt: editDa, selectSt: selectDa}">
                                <span v-show="!editDa">{{ term }}</span>
                                <input type="text" class="contentVal" v-show="editDa" v-on:blur="editBlurMe" v-bind:value="term">
                            </div>
                        </div>
                        <div class="ura card" v-bind:class="{editCard: editDa}" v-on:click.prevent.stop="sideFlip" v-else>
                            <div class="fusenCl" v-bind:class="{greenCl : fusenCo == 'green', yellowCl : fusenCo == 'yellow', redCl : fusenCo == 'red'}"></div>
                            <img class="cardImg" v-show="imageUra" v-bind:src="imageUra" alt="画像を読み込めませんでした。">
                            <div class="contentSt conUra" v-on:blur="editBlurMe" v-bind:class="{editSt: editDa, selectSt: selectDa}">
                                <span v-show="!editDa">{{ desc }}</span>
                                <textarea-autosize class="contentVal uraTarea" v-show="editDa" v-on:blur.native="editBlurMe" v-bind:value="desc" />
                            </div>
                        </div>
                    </transition>
                </div>
                <div class="pageBtns" v-show="(keys.length > 0)">
                    <button v-on:click.prevent.stop="prev">前</button>
                    <button v-on:click.prevent.stop="next">次</button>
                </div>
            </div>
            <div class="toolPos" v-on:click.prevent.stop="toolAppearMe">
                <div class="toolBar">
                    <div class="ToolThumb">
                        <img class="toolbarToggle" src="../../public/toolbar.svg" alt="">
                    </div>
                    <div class="buttons">
                        <div class="btnUpper">
                            <button v-on:click.prevent="fusenMe"><ons-icon icon="fa-bookmark" class="toolIcon" size="30px"/><span class="toolBtnTxt">ブックマーク</span></button>

                            <button v-on:click.prevent="editMe" v-if="!editDa"><ons-icon icon="fa-edit" class="toolIcon" size="30px"/><span class="toolBtnTxt">書き換え</span></button>
                            <button v-on:click.prevent="editMe" id="editDefine" v-else><ons-icon icon="fa-check" class="toolIcon" size="30px"/><span class="toolBtnTxt">確定</span></button>

                            <button v-on:click.prevent="newImgDiaShow"><ons-icon icon="fa-image" class="toolIcon" size="30px"/><span class="toolBtnTxt">画像挿入</span></button>
                        </div>
                        <div class="btnLower">
                            <button v-on:click.prevent="addCardMe"><ons-icon icon="fa-plus" class="toolIcon" size="30px"/><span class="toolBtnTxt">カード追加</span></button>
                            <button v-on:click.prevent="removeCardMe"><ons-icon icon="fa-trash" class="toolIcon" size="30px"/><span class="toolBtnTxt">カード削除</span></button>
                            <button v-on:click.prevent="shuffleMe"><ons-icon icon="fa-random" class="toolIcon" size="30px"/><span class="toolBtnTxt">シャッフル</span></button>
                        </div>
                    </div>
                </div>
            </div><!--toolPos-->

        <v-ons-dialog
        cancelable
        :visible.sync="newImgDia"
        >
            <p class="dialogContent dialogTitle">画像挿入</p>
            <div class="dialogContent dialogInput">
                <v-ons-input type="text" class="newImgInp" placeholder="画像URL" v-on:keyup.enter="imageReg" v-model="imageURL" />
            </div>
            <div class="dialogContent dialogBtn">
                <v-ons-button modifier="quiet" v-on:click="imageReg">OK</v-ons-button>
                <v-ons-button modifier="quiet" v-on:click="newImgDia = false">CANCEL</v-ons-button>
            </div>
        </v-ons-dialog>
        </div>
    </div>
</template>

