<script>
import { mapGetters } from 'vuex'
import firebase from "firebase/app"

export default {
    components: {
    },
    name: 'Learn',
    data: function(){
        return {
            database: firebase.database(),
            cardPath: this.$route.params.cardsName.slice(1),
            get: {},
            cardTitle: '',
            objInd: 0,
            whiteActive: false,
            onOff: false,
            btnOn: false,
            disableFlip: false,
            sides: true,
            editDa: false,
            selectDa: false,
            cardIf: true,
            newImgDia: false,
            imageURL: '',
            flipName: 'flip'
        }
    },
    computed: {
        ...mapGetters([
            'user',
            'isLoggedIn'
        ]),
        keys: function() {
            return (this.get) ? Object.keys(this.get): {};
        },
        term: function() {
            return this.get[this.keys[this.objInd]][0];
        },
        splitTerm: function() {
            return this.term.split('')
        },
        desc: function() {
            return this.get[this.keys[this.objInd]][1];
        },
        splitDesc: function() {
            return this.desc.split('')
        },
        image: function() {
            return this.get[this.keys[this.objInd]][2];
        },
        imageUra: function() {
            return this.get[this.keys[this.objInd]][3];
        },
        fusenCo: function() {
            return this.get[this.keys[this.objInd]][4];
        },
    },
    methods: {
        sideFlip: function() {
            if(!this.disableFlip) {
                this.flipName = "flip";
                this.sides = !this.sides
            }
        },
        next:function() {
            if(!this.disableFlip){
                this.flipName = "";
                this.objInd++;
                if(this.objInd > this.keys.length - 1) {
                    this.objInd = 0;
                }
                this.sides = true;
            }
        },
        prev: function() {
            if(!this.disableFlip){
                this.flipName = "";
                this.objInd--;
                if(this.objInd < 0) {
                    this.objInd = this.keys.length - 1;
                }
            }
            this.sides = true;
        },
        modal: function() {
            alert('ok')
        },
        /* ツールバー */
        toolAppearMe: function() {
            let toolBar = document.getElementsByClassName('toolBar')[0];
            toolBar.style.right = "0";
        },
        toolLeaveMe: function() {
            let toolBar = document.getElementsByClassName('toolBar')[0];
            toolBar.style.right = "-100vw";
        },
        fusenMe: function() {
            let color = "";
            switch(this.fusenCo) {
                case '': 
                    color = "green";
                    break;
                case 'green': 
                    color = "yellow";
                    break;
                case 'yellow': 
                    color = "red";
                    break;
                case 'red': 
                    color = "";
                    break;
            }
            this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' + this.objInd).update({
                4: color
            });
        },
        //編集まわり
        editMe: function() {
            this.disableFlip = !this.disableFlip;
            this.editDa = !this.editDa;
            this.$nextTick(function() {
                let content = document.getElementsByClassName('contentVal')[0];
                content.focus();
            })
        },
        editBlurMe: function() {
            let content = document.getElementsByClassName('contentVal')[0].value;
            if(this.sides) {
                this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' + this.objInd).update({
                    0: content
                });
            }else {
                this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' + this.objInd).update({
                    1: content
                });
            }
        },
        newImgDiaShow: function() {
            this.newImgDia = true;
            this.$nextTick(function() {
                let imgInp = document.getElementsByClassName('newImgInp')[0];
                imgInp.focus();
            })
        },
        imageReg: function() {
            if(this.sides) {
                this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' + this.objInd).update({
                    2: this.imageURL
                });
            }else {
                this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' + this.objInd).update({
                    3: this.imageURL
                })
            }
            this.newImgDia = false;
        },
        addCardMe: function() {
            let maxAddOne = 0;
            if(this.get) {
            maxAddOne = Math.max(...this.keys) + 1;
            }
            this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data').update({
                [maxAddOne]: ['', '', '', '', '']
            });
            this.$ons.notification.toast('カードを追加しました', { timeout: 1000, animation: 'fall' });
            this.objInd = this.keys.length - 1;
        },
        removeCardMe: function() {
            if(this.keys.length > 0) {
                this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data/' + this.keys[this.objInd]).remove();
                let that = this;
                this.database.ref('user/' + that.user.uid + '/cards/' + that.cardPath + '/data/').get()
                .then(function(snap){
                    let newSt = {};
                    let copySt = snap.toJSON();
                    let copyKeys = Object.keys(copySt);
                    for(let i = 0; i < copyKeys.length; i++) {
                    newSt[i] = copySt[copyKeys[i]];
                    }
                    that.database.ref('user/' + that.user.uid + '/cards/' + that.cardPath + '/data/').set(
                    newSt
                    )}
                )
                if(this.objInd >= Object.keys(this.get).length - 1) {
                    this.objInd = 0;
                }
                this.$ons.notification.toast('カードを削除しました', { timeout: 1000, animation: 'fall' });
            }
        },
        shuffleMe: function() {
            if(this.keys.length > 0){
                let newSt = {};
                let copySt = {};
                Object.assign(copySt, this.get);
                let copyLen = Object.keys(copySt).length;
                let assigned = [];
                for(let nIdx = 0; nIdx < copyLen; nIdx++) {
                    let randNum = Math.floor(Math.random() * Object.keys(copySt).length);
                    while(assigned.includes(randNum)) {
                        randNum = Math.floor(Math.random() * Object.keys(copySt).length);
                    }
                    assigned.push(randNum);
                    newSt[nIdx] = copySt[randNum];
                }
                this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath).update({
                    data: newSt
                })
                this.$ons.notification.toast('カードをシャッフルしました', { timeout: 1000, animation: 'fall' });
                
            }
        }
    },
    created: function() {
        let that = this;
        this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath).get().then(function(snap) {
            let get = snap.toJSON();
            if(get[0] == {}) {
                let empObj = [" ", " ", "", "", ""];
                that.database.ref('user' + that.user.uid + '/cards/' + that.cardPath).update({
                    data: empObj
                })
            }
        })
        if(this.get[0] == {}) {
            let empObj = [" ", " ", "", "", ""];
            this.database.ref('user' + this.user.uid + '/cards/' + this.cardPath).update({
                data: empObj
            })
        }
        this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath).on('value', function(snap) {
            that.get = snap.toJSON().data;
            that.cardTitle = snap.toJSON().name;
        })
    },
    mounted: function() {
        let that = this;

        this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath).get()
        .then(function(snap) {
            if(Object.keys(snap.toJSON().data).length < 2){
                that.toolAppearMe();
                that.$nextTick(function() {
                    let content = document.getElementsByClassName('contentSt')[0];
                    content.focus();
                })
            }
        })
    },
}
</script>
