<template>
    <div class="test">
        <div class="wrapper">
                <div class="cardTes" v-on:click="startClk" v-if="start"></div>
                <div class="cardTes" v-else> 
                    <div class="descFlex">
                        <div class="cardDesc">
                            <pre>{{ desc }}</pre>
                        </div><!--cardDesc-->
                    </div>
                    <div class="ans">
                        <p class="cardTerm" v-bind:class="{white: whiteActive}">{{ term }}</p>
                        <p class="message" v-show="msgIf">{{msg}}</p>
                    </div>
                </div><!--card-->
                <div class="start" v-on:click="startClk" v-if="start">
                    <p>Click to Start</p>
                </div>
                <div class="choices" v-else>
                    <button class="choice" v-for="(item, key) in choices" v-bind:key="key" v-on:click="chose(item)" v-bind:disabled="btnDis">{{ item }}</button>
                </div>
                <div class="timer" v-if="timerIf" v-on:animationend="animEnd"></div>
                <button class="skipBtn" v-on:click="skip" v-if="!start">Skip</button>
        </div><!--wrapper-->


    </div>
</template>

<style scoped>
    .test {
        user-select: none;
    }
    .cardsTitle {
        font-size: 1.5rem;
        margin: 20px 0;
    }
    .wrapper {
        width: 100vw;
        padding: 0 15vw;
    }
    .cardTes {
        overflow-wrap: break-word;
