        font-weight: bolder;
    }
    .descFlex {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        padding: 15px 20px;
    }
    .descImg {
        width: auto;
        max-width: 200px;
        height: auto;
        max-width: 200px;
        border: 1px solid gray;
    }
    .cardDesc {
        margin: 10px 10px 5px 35px;
        text-align: center
    }
    .white {
        color: rgba(0, 0, 0, 0)
    }
    .start {
        font-size: 1.5rem;
        font-weight: bolder;
