    }
    .timer {
        height: 10px;
        animation: timerAnim 7.5s;
    }
    /* .timerMove {
    } */
    .skipBtn {
        padding: 5px 20px;
        background-color: white;
        border: 1px gray solid;
        border-radius: 3px;
    }
    /*** ANIMATION ***/
    @keyframes timerAnim {
        0% {
            width: 100%;
            background-color: rgb(134, 227, 187);
        }
        50% {
            background-color: rgb(134, 227, 187);
        }
        100% {
            width: 0%;
            background-color: rgb(255, 30, 30);
        }
    }
    /*Media query*/
    @media screen {
        
    }
</style>

<script>
import { mapGetters } from 'vuex'
import firebase from "firebase/app"

export default {
    components: {
    },
    name: 'Test',
    data: function(){
        return {
            database: firebase.database(),
            cardPath: this.$route.params.cardsName.slice(1),
            storage: {},
            whiteActive: true,
            start: true,
            objInd: 0,
            msg: 'Correct!',
            msgIf: false,
            btnDis: null,
            timerIf: true,
        }
    },
    computed: {
        ...mapGetters([
            'user',
            'isLoggedIn'
        ]),
        keys: function() {
            return Object.keys(this.storage);
        },
        term: function() {
            return this.storage[this.objInd][0];
        },
        desc: function() {
            return this.storage[this.objInd][1];
        },
        image: function() {
            return this.storage[this.objInd][2];
        },
        vForTerm: function() {
            return Object.keys(this.storage);
        },
        choices: function() {
            let choices = [];
            let correct = this.keys[this.objInd];
            let incorrect = this.keys.filter(x => x != correct);
            for(let i = 0; i < 3; i++) {
                let incIdx = Math.floor(Math.random() * incorrect.length);
                choices.push(incorrect[incIdx]);
                incorrect = incorrect.filter(x => x != incorrect[incIdx]);
            }
            let corIdx = Math.floor(Math.random() * 4);
            choices.splice(corIdx, 0, correct);
            return choices.map(x => this.storage[x][0])
        }
    },
    methods: {
        startClk: function() {
            this.start = false;
            let timer = document.getElementsByClassName('timer')[0];
            timer.style.animationPlayState = 'running';
        },
        chose: function(item) {
            this.msgIf = true;
            this.btnDis = 'disabled';
            let msg = document.getElementsByClassName('message')[0];
            let timer = document.getElementsByClassName('timer')[0];
            timer.style.animationPlayState = "paused";
            if(this.term == item) {
                this.msg = "Correct!";
                msg.style.backgroundColor = "rgba(114, 227, 167, 0.7)"
            }else {
                this.msg = "Incorrect!";
                msg.style.backgroundColor = "rgba(232, 0, 0, 0.7)"
            }
            this.whiteActive = false;
            let that = this;
            let keys = Object.keys(that.storage);
            let nextIdx = Math.floor(Math.random() * keys.length);
            while(this.objInd == nextIdx) {
                nextIdx = Math.floor(Math.random() * keys.length);
            }
            setTimeout(function() {
            that.whiteActive = true;
            that.objInd = nextIdx;
            that.msgIf = false;
            that.btnDis = null;
            timer.style.animationPlayState = "running";
            that.timerIf = false;
                
            that.$nextTick(() => {
                that.timerIf = true;
            });
            }, 2500)
        },
        skip: function() {
            let keys = Object.keys(this.storage);
            let nextIdx = Math.floor(Math.random() * keys.length);
            while(this.objInd == nextIdx) {
                nextIdx = Math.floor(Math.random() * keys.length);
            }
            this.objInd = nextIdx;
            this.timerIf = false;
                
            this.$nextTick(() => {
                this.timerIf = true;
            });
        },
        animEnd: function() {
            let message = document.getElementsByClassName('message')[0];
            let that = this;
            this.msgIf = true;
            this.msg = 'Time Up!';
            this.btnDis = 'disabled';
            message.style.backgroundColor = 'rgba(232, 0, 0, 0.7)';
            setTimeout(function() {
                that.msgIf = false;
                that.btnDis = null;
                let keys = Object.keys(that.storage);
                let nextIdx = Math.floor(Math.random() * keys.length);
                while(that.objInd == nextIdx) {
                    nextIdx = Math.floor(Math.random() * keys.length);
                }
                that.objInd = nextIdx;
                that.timerIf = false;
                    
                that.$nextTick(() => {
                    that.timerIf = true;
                });
            }, 1500);
        }
    },
    created: function() {
        let that = this;
        this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath + '/data').get()
        .then(function(snap) {
            that.storage = snap.toJSON();
        })
    },
    mounted: function() {
            this.objInd = Math.floor(Math.random() * Object.keys(this.storage).length);
            let timer = document.getElementsByClassName('timer')[0];
            timer.style.animationPlayState = 'paused';
    },
}
</script>
